@import "helpers/variables";
@import "helpers/mixins";
@import "helpers/functions";
@import "vendor/libs";
@import "base/fonts";
@import "base/general";
@import "../blocks/modules";

// COMMON STYLES
html {
    font-family: $font-oswald-regular, sans-serif;
    font-size: $text;
}
.block {
    display: flex;
    flex-flow: column wrap;
}
.content {
    flex-basis: 50%;
}
.media {
    flex-basis: 50%;
}
.btn {
    &__wrapper {
        display: flex;
        justify-content: center;
    }
    padding: rem(3) rem(43);
    border: 1px solid $dark-gray;
    background: transparent;
    cursor: pointer;
    text-transform: uppercase;
    color: $white;
}
.soc-media__icon {
    font-size: $text;
}
.title {
    font-size: $title;
    color: $light-black;
    line-height: 30px;
}
.heading {
    font-size: $heading;
    color: $gray;
}
.title,
.heading {
    text-transform: uppercase;
}
.text {
    font-size: $text;
    color: $gray;
}
.nav-text {
    font-size: $nav-text;
    color: $gray;
}
.quote {
    font-size: $quote;
}
.wrapper {
    max-width: 1300px;
    margin: 0 auto;
}
.header,
.main,
.footer {
    max-width: 100%;
}
